import { customElement, property } from 'lit/decorators.js';
import {
  CSSResultGroup,
  html,
  LitElement,
  TemplateResult,
  unsafeCSS,
} from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import cssStyles from './zwc-image_styles.scss?inline';

@customElement('zwc-image')
export class ZwcImage extends LitElement {
  @property({ type: String })
  src = '';

  @property({ type: String })
  size = '';

  @property({ type: Boolean })
  rounded = false;

  @property({ type: Boolean })
  centered = false;

  static override styles?: CSSResultGroup | undefined = unsafeCSS(cssStyles);

  protected override render(): TemplateResult {
    const classes = {
      [`img-${this.size}`]: this.size,
      'img-rounded': this.rounded,
      'img-centered': this.centered,
    };

    return html`<img
      src="${this.src}"
      alt="image"
      class="${classMap(classes)}"
    />`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-image': ZwcImage;
  }
}
